import getPaginationHeaders from '../utils/get-pagination-headers'
import { doRequest, host, sendRequest } from './Api'

const path = '/tce'

export const getBaseUrl = () => `${host}${path}`

export function CriaTermoDeCompromisso(dados, onSuccess, onError){
    const request = {
        url : `${getBaseUrl()}`,
        params : {
            method : 'POST',
            body : JSON.stringify(dados)
        },
        onSuccess,
        onError
    }
    
    doRequest(request)
}

export function ConsultaTermoDeCompromissoPeloRegistro(registro, onSuccess, onError){
    const request = {
        url : `${getBaseUrl()}/${registro}`,
        onSuccess,
        onError
    }
    
    doRequest(request)
}

export function ListaTermosDeCompromisso(pagination){
    return sendRequest({
        url : `${getBaseUrl()}`,
        headers: getPaginationHeaders(pagination, { defaultPageSize: 80 }),
    })
}