import _ from "lodash";
import getPaginationHeaders from "../utils/get-pagination-headers";
import { host, sendRequest } from "./Api";

const path = "/estagios";

export const getBaseUrl = () => `${host}${path}`;

export function buscaEstagios(search, pagination) {
	const searchParams = _.pick(search, ["registro", "nome", "curso"]);

	const searchQuery = Object.keys(searchParams).reduce(
		(prev, param) => {
			const value = !searchParams[param];
			if(!value) return prev;

			const prefix = !prev ? "?" : "&";
			return `${prev}${prefix}${param}=${value}`
		},
		""
	)

	return sendRequest({
		url: `${getBaseUrl()}/search${searchQuery}`,
		headers: getPaginationHeaders(pagination),
	});
}

export function consultaEstagiosPeloRegistro(registro, pagination) {
	return sendRequest({
		url: `${getBaseUrl()}/${registro}`,
		headers: getPaginationHeaders(pagination),
	});
}

export function listaEstagiosPorSituacao(situacao, pagination) {
	return sendRequest({
		url: `${getBaseUrl()}`,
		headers: {
			...getPaginationHeaders(pagination),
			situacao,
		}
	});
}

export function listaEstagios(pagination) {
	return sendRequest({
		url: `${getBaseUrl()}`,
		headers: getPaginationHeaders(pagination, { defaultPageSize: 80 }),
	});
}
