import React, { Component } from 'react';

import { Api } from '../../../Api';
import { Email, Telefone } from '../../../Componentes';
import { AlunoCell } from '../../../Componentes/AlunoCell';
import { formatDatePtBr } from '../../../utils';

export default class ListaDeEstagios extends Component {
    api = new Api()

    constructor(){
        super()
        this.state = {
            listaDeEstagios : [],
            loadingIds: [],
        }
    }

    componentDidMount(){
        this.api.estagios
            .listaEstagios({ pageSize: 150 })
            .then((listaDeEstagios) => this.setState({listaDeEstagios}))
            .catch((erro) => console.log("erro", erro));
    }

    linkParaBaixarTermo(registro = ''){
        const {api} = this.props
        return `${api.TermosDeCompromisso.getBaseUrl()}/${registro}/baixar`
    }

    isLoading(id){
        return this.state.loadingIds.includes(id);
    }

    setLoading(id){
        this.setState(state => state.loadingIds.includes(id) ? state : ({
            ...state,
            loadingIds: [...state.loadingIds, id]
        }))
    }

    setLoaded(id){
        this.setState(state => !state.loadingIds.includes(id) ? state : ({
            ...state,
            loadingIds: state.loadingIds.filter(v => v !== id)
        }))
    }

    async baixar(tce){
        const {api} = this.props
        const token = api.Auth.accessToken()
        const link = this.linkParaBaixarTermo(tce)

        this.setLoading(tce);
        await fetch(link, {
            headers : {
                "Authorization" : `Bearer ${token}`
            }
        })
        .then(res => res.arrayBuffer())
        .then(buffer => {
            const blob = new Blob([buffer], { type: "application/pdf" });
            const blobUrl = URL.createObjectURL(blob);

            this.setState(prevState => {
                const {listaDeEstagios} =  prevState
                
                const estagio = listaDeEstagios.filter(estagio => estagio.tce === tce)[0]
                const estagioAtualizado = {...estagio, blobUrl}

                const listaDeEstagiosAtualizada = listaDeEstagios.reduce( (listaNova, valorOriginal) => {
                    listaNova.push( valorOriginal.tce === tce ? estagioAtualizado : valorOriginal )
                    return listaNova
                }, [])

                return ({
                    ...prevState,
                    listaDeEstagios : [...listaDeEstagiosAtualizada]
                })
            })
        });
        this.setLoaded(tce);
    }

    render() {
        const {listaDeEstagios} = this.state

        return (
            <div>
                <h5>Estágios</h5>
                <table className="table table-striped table-hover">
                    <thead>
                        <tr>
                            <th></th>
                            <th>Estagiário(a)</th>
                            <th>Contrato</th>
                            <th>Orientação/Supervisão</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                    {
                        listaDeEstagios.map((estagio, i) => {
                            const { vigencia, estagiario, campo_do_estagio, orientador, supervisor } = estagio

                            return (
                                <tr key={estagio.id}>
                                    <td>{i+1}</td>
                                    <td>
                                        <AlunoCell aluno={estagiario} />
                                    </td>
                                    <td>
                                        <div><small>N.: {estagio.tce } | <strong>{estagio.situacao }</strong></small></div>
                                        <div><small>Vigência: { `${formatDatePtBr(new Date(vigencia.inicio))} a ${formatDatePtBr(new Date(vigencia.fim))}` }</small></div>
                                        <div><small>Local: {campo_do_estagio.nome}</small></div>
                                    </td>
                                    <td>
                                        <div><small>Orientador: {orientador.nome}</small></div>
                                        <div className="border-bottom"/>
                                        <div><small>Supervisor: {supervisor.nome}</small></div>
                                        <div><small><Telefone value={supervisor.telefone}/> {supervisor.email && <> | <Email value={supervisor.email}/></>}</small></div>
                                    </td>
                                    <td>
                                        { estagio.blobUrl === undefined
                                            ? this.isLoading(estagio.tce)
                                                ? "Baixando..."
                                                : <button onClick={() => this.baixar(estagio.tce)}>Baixar</button>
                                            :
                                                <button onClick={() => window.open(estagio.blobUrl, '_blank')}>Abrir</button>
                                        }
                                        {/* <button onClick={() => this.aditivarTermo(estagio)}>Aditar</button> */}
                                    </td>
                                </tr>
                            )
                        })
                    }
                    </tbody>
                </table>
            </div>
        )
    }
}
